import useDocumentSize from '@cetus/hooks/src/useDocumentSize'
import { Icon } from '@cetus/ui-kit'
import { Button, Divider, HStack, MenuButton, Skeleton, Text } from '@chakra-ui/react'
import FeeSelect from '.'
import { baseFeeStepConfig, feeConfigs } from './config'
import { PoolType, SelectFeeProps } from './type'

export function FeeSelectBlock<T extends PoolType>({
  poolType = 'clmm',
  value,
  options,
  onChange,
  loading,
  disabled,
  isShowSelect = true
}: Omit<SelectFeeProps<T>, 'children'>) {
  const { size } = useDocumentSize()
  const { title, label, color } = feeConfigs[poolType]
  return (
    <FeeSelect<T> poolType={poolType} value={value} options={options} onChange={onChange} placement="bottom-end">
      {({ isOpen, onClick }) => (
        <MenuButton
          as={Button}
          variant="unstyled"
          isDisabled={disabled}
          p="3px 3px 3px 8px"
          borderRadius="8px"
          border="1px solid"
          borderColor="border"
          display="flex"
          bg="bg_secondary"
          justifyContent="space-between"
          h={{ base: '40px', lg: '32px' }}
          w={{ base: '100%', lg: 'auto' }}
          cursor="pointer"
          onClick={() => {
            if (isShowSelect) {
              onClick()
            }
          }}
        >
          <HStack w="100%" h={{ base: '32px', lg: '24px' }} justify={{ base: 'space-between', lg: 'center' }}>
            <Text color="primary" fontSize={{ base: '14px', lg: '12px' }} fontWeight="500">
              {title}
            </Text>
            {loading ? (
              <Skeleton w="84px" h={{ base: '32px', lg: '24px' }} />
            ) : (
              <HStack borderRadius="4px" bg="primary_opacity.10" p="5px 8px" gap="4px">
                {label && <Text fontSize="12px">{label}</Text>}
                {poolType === 'clmm' ? (
                  <Text color="primary" fontSize="14px">
                    {value?.feeDisplay || '--%'}
                  </Text>
                ) : (
                  <>
                    <Text color="primary" fontSize="14px">
                      {value?.feeDisplay || '--%'}
                    </Text>
                    <Divider orientation="vertical" h="10px" borderColor="white_color_opacity.30" />
                    <Text color="primary" fontSize="14px">
                      {value?.binStep || baseFeeStepConfig[value?.fee || '']} bps
                    </Text>
                  </>
                )}

                {isShowSelect ? (
                  <Icon
                    fontSize="12px"
                    xlinkHref="#icon-icon_arrow"
                    transition="transform 0.5s"
                    transform={isOpen ? 'rotate(180deg)' : 'rotate(0deg)'}
                  />
                ) : null}
              </HStack>
            )}
          </HStack>
        </MenuButton>
      )}
    </FeeSelect>
  )
}
